.location {
    padding: 4.4rem 0 17rem;

    position: relative;

    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 60px;
        background: #E5E5E5;
    }
}

.location__title {
    font-family: $oswald;
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-align: center;
    text-transform: uppercase;

    margin-bottom: 5rem;

    @include breakpoint($tablet) {
        font-size: 3rem;
    }

    @include breakpoint($mobile) {
        font-size: 2.2rem;

        margin-bottom: 3.8rem;
    }
}

.location__inner {
    display: flex;

    @include breakpoint($tablet-bg) {
        flex-direction: column;
    }
}

.location__map {
    width: 55%;
    cursor: pointer;

    img {
        width: 100%;
    }

    @include breakpoint($tablet-bg) {
        width: 100%;
    }

    @include breakpoint($mobile) {
        overflow: hidden;
        img {
            width: auto;
        }

        margin-bottom: 3rem;
    }
}

.location__contacts {
    width: 45%;
    background: #EDEDED;
    padding: 7.6rem 6.9rem 0;

    margin-left: 2rem;

    @include breakpoint($tablet-bg) {
        width: 100%;

        margin-left: 0;

        padding: 5rem 4.9rem;
    }

    @include breakpoint($tablet) {
        padding: 4rem 3.9rem;
    }

    @include breakpoint($mobile) {
        padding: 3rem 2.7rem 2.5rem;
    }
}

.location__name {
    font-family: $oswald;
    font-size: 2.4rem;
    color: $black;

    margin-bottom: 3rem;

    @include breakpoint($tablet) {
        font-size: 2.1rem;
    }
}

.location__address {
    font-style: normal;
    font-size: 1.8rem;
    color: $green;
    font-weight: 400;
    text-decoration: underline;

    margin-bottom: 3rem;

    @include breakpoint($tablet) {
        font-size: 1.6rem;
    }
}

.location__info {
    font-size: 2.4rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 3rem;

    @include breakpoint($tablet) {
        font-size: 2rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.6rem;
    }
}

.location__number {
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;

    a {
        color: inherit;
        text-decoration: none;
        font-size: 2.4rem;

        @include breakpoint($tablet) {
            font-size: 2.2rem;
        }
    }

    margin-bottom: 0.5rem;

    @include breakpoint($tablet) {
        font-size: 1.6rem;
    }

    @include breakpoint($mobile) {
        font-size: 1.5rem;
    }
}

.location__email {
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;

    a {
        color: inherit;
    }

    @include breakpoint($tablet) {
        font-size: 1.6rem;
    }
}