.about {
    padding: 4.4rem 0 10rem;

    position: relative;

    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 60px;
        background: #E5E5E5;

        @include breakpoint($mobile) {
            height: 40px;
        }
    }

    @include breakpoint($mobile) {
        padding: 3.5rem 0 8rem;
    }
}

.about__title {
    font-family: $oswald;
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-align: center;
    text-transform: uppercase;

    margin-bottom: 4rem;

    @include breakpoint($tablet) {
        font-size: 3rem;
    }
    
    @include breakpoint($mobile) {
        font-size: 2.2rem;

        margin-bottom: 2rem;
    }
}

.about__inner {
    display: flex;
    align-items: center;

    background: #EDEDED;

    padding: 5.5rem 15rem 5.5rem 4.6rem;

    @include breakpoint($tablet-bg) {
        padding: 5.5rem 4.6rem;
    }

    @include breakpoint($tablet) {
        flex-direction: column;

        padding: 5.5rem 4rem;
        align-items: flex-start;
    }

    @include breakpoint($mobile) {
        padding: 3.5rem 2.2rem;
    }
}

.about__img {
    width: 30%;
    text-align: center;
    // img {
    //     width: 100%;
    // }

    @include breakpoint($tablet-bg) {

        img {
            width: 100%;
        }
    }

    @include breakpoint($mobile) {

        width: 100%;

        margin-bottom: 4rem;
    }

}

.about__content {
    width: 70%;
    font-size: 1.8rem;
    color: #3A4244;
    font-weight: 400;
    line-height: 1.6;

    padding-left: 5rem;

    p {
        position: relative;
        margin-bottom: 5.4rem;

        &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: -2.7rem;
            left: 0;
            z-index: 1;
            width: 111px;
            height: 4px;
            background: #0F0F0F;
        }

        &:last-child {
            margin-bottom: 0;
            &::after {
                display: none;
            }
        }
    }

    @include breakpoint($tablet) {
        width: 100%;
        font-size: 1.5rem;
        padding-left: 0;
    }

    @include breakpoint($mobile) {
        font-size: 1.4rem;
    }
}