.employees {
    padding: 7.8rem 0 10rem;
    position: relative;

    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 60px;
        background: #E5E5E5;

        @include breakpoint($mobile) {
            height: 40px;
        }
    }

    @include breakpoint($mobile) {
        padding: 7.8rem 0 8rem;
    }
}

.employees__title {
    font-family: $oswald;
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-align: center;

    text-transform: uppercase;
    margin-bottom: 4rem;

    @include breakpoint($tablet) {
        font-size: 3rem;
    }

    @include breakpoint($mobile) {
        font-size: 2.2rem;
        text-align: left;

        margin-bottom: 1.6rem;
    }
}

.employees__inner {
    display: flex;

    flex-wrap: wrap;
    // justify-content: space-between;

    // margin: -6px -6px;
}

.employees__item {
    margin: 0 10px;
    width: calc(25% - 20px);
    // width: 24%;

    // box-shadow: 0 4px 10px rgba($black, 0.15);
    background: #EDEDED;
    border-radius: 3px;

    // padding: 0 1.1rem;
    // margin: 0 1.1rem;
    margin-bottom: 2rem;

    transition: background 0.1s ease-in-out, box-shadow 0.2s ease-in-out;
    &:hover {
        background: $white;
        box-shadow: 0 4px 10px rgba($black, 0.15);

        .btn--white {
            background: $yellow;
            color: $black;
        }
    }

    &:nth-last-child(-n+2) {
        display: none;
    }

    @include breakpoint($tablet-bg) {
        width: calc(33% - 20px);
    }

    @include breakpoint($tablet) {
        width: calc(50% - 20px);
    }

    @include breakpoint($mobile) {
        width: 100%;
        margin: 0;

        margin-bottom: 2rem;

        &:not(:nth-child(-n+4)) {
            display: none;
        }
    }
}

.employees__photo {
    background: #EDEDED;
    img {
        width: 100%;
        height: 100%;
        border-radius: 3px;
    }
}

.employees__item-content {
    padding: 2.2rem 5rem 3.7rem;

    @include breakpoint($tablet) {
        padding: 2.2rem 1.3rem;
    }
}

.employees__surname {
    font-family: $oswald;
    font-size: 2.4rem;
    color: $green;
    font-weight: 400;
    text-align: center;

    @include breakpoint($tablet) {
        font-size: 2.2rem;
    }
}

.employees__name {
    font-family: $oswald;
    font-size: 2.4rem;
    color: $black;
    font-weight: 400;

    text-align: center;
    margin-bottom: 2rem;

    @include breakpoint($tablet) {
        font-size: 2.2rem;
    }
}

.employees__spec {
    position: relative;
    font-size: 1.4rem;
    color: #979797;
    font-weight: 400;

    text-align: center;

    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 5rem;
        left: 50%;
        z-index: 1;
        width: 112px;
        height: 4px;
        background: $black;

        transform: translateX(-50%);
    }

    margin-bottom: 7rem;
}

.employees__btn {
    margin-bottom: 2rem;

    &:last-child {
        margin-bottom: 0;
    }
}

.employees__more {
    text-align: center;

    padding-top: 3rem;

    @include breakpoint($mobile) {
        padding-top: 1.7rem;
    }
}